import React from "react";
import Mousetrap from "mousetrap";
import { Icon, Menu, Dropdown } from "semantic-ui-react";

import {
  // Insert
  insertQuote,
  insertCounter,
  insertQuantity,
  insertImage,
  insertCode,
  insertLink,
  insertTable,

  // Show
  showHelp,
  showStats,
  showSlides,
  showAgenda,
  showEditor
} from "./commands";

import "./CommandsMenu.css";

Mousetrap.bind("ctrl+1", showEditor);
Mousetrap.bind("ctrl+2", showAgenda);
// Mousetrap.bind("ctrl+3", showSlides);
Mousetrap.bind("ctrl+4", showStats);
Mousetrap.bind("ctrl+5", showHelp);

export default function CommandsMenu({ disabled, getView }) {
  // const subscription = props.source.subscribe();
  // return () => {
  //   // Clean up the subscription
  //   subscription.unsubscribe();
  // };

  const items = [
    ["file", "Image", insertImage, false],
    ["linkify", "Link", insertLink, false],
    ["code", "Code", insertCode, false],
    ["quote left", "Quote", insertQuote, false],
    ["th", "Table", insertTable, false],
    ["sort numeric up", "Counter", insertCounter, false],
    ["percent", "Quantity", insertQuantity, false]
  ];
  return (
    <Dropdown
      item
      icon={null}
      floated="right"
      disabled={disabled}
      trigger={
        <>
          <Icon name="code" /> Commands
        </>
      }
      className="link"
      as="a"
    >
      <Dropdown.Menu className="CommandsMenu">
        <Dropdown.Header>Insert</Dropdown.Header>
        {items.map(([icon, text, onClick, disabled], key) => (
          <Dropdown.Item
            {...{
              key,
              icon,
              text,
              disabled,
              onClick: () => {
                const v = getView();
                v.focus();
                onClick(v);
              }
            }}
          />
        ))}
        <Dropdown.Divider />
        <Dropdown.Header>Format</Dropdown.Header>
        {/* <Dropdown.Item */}
        {/*   icon="arrows alternate vertical" */}
        {/*   text="Fold Selection" */}
        {/*   disabled */}
        {/* /> */}

        <Dropdown.Header>View</Dropdown.Header>
        <Dropdown.Item
          icon="file alternate outline"
          text="Show Editor"
          description="ctrl + 1"
          onClick={showEditor}
        />
        <Dropdown.Item
          icon="clipboard list"
          text="Show Agenda"
          description="ctrl + 2"
          onClick={showAgenda}
        />
        {/* <Dropdown.Item */}
        {/*   icon="tv" */}
        {/*   text="Show Slides" */}
        {/*   description="ctrl + 3" */}
        {/*   onClick={showSlides} */}
        {/* /> */}
        <Dropdown.Item
          icon="chart line"
          text="Show Stats"
          description="ctrl + 4"
          onClick={showStats}
        />
        <Dropdown.Item
          icon="help"
          text="Show Help"
          description="ctrl + 5"
          onClick={showHelp}
        />
        {/* <Dropdown.Item */}
        {/*   disabled */}
        {/*   icon="share square" */}
        {/*   text="Show Preview" */}
        {/*   description="ctrl + shift + p" */}
        {/*   onClick={ */}
        {/*     /\* TODO: generate preview *\/ */}
        {/*     () => alert("TODO: generate preview") */}
        {/*   } */}
        {/* /> */}
      </Dropdown.Menu>
    </Dropdown>
  );
}
