import React, { Component } from "react";
import { Sidebar, Segment, Icon, Responsive } from "semantic-ui-react";
import PropTypes from "prop-types";
import { Menu } from "semantic-ui-react";

import SideMenu from "./Menu/Menu";
import Editor from "./Editor/Editor.js";

import "./App.css";

class App extends Component {
  static propTypes = {
    firebase: PropTypes.object.isRequired
  };

  state = { visible: true, width: 0, height: 0 };

  componentDidMount() {
    this.updateWindowDimensions();
    window.addEventListener("resize", this.updateWindowDimensions);
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.updateWindowDimensions);
  }

  updateWindowDimensions = () => {
    this.setState({ width: window.innerWidth, height: window.innerHeight });
  };

  render() {
    const { visible, width } = this.state;
    const { firebase } = this.props;

    const isSmallScreen = width < 1100;

    return (
      <>
        {visible === false && (
          <Menu className="Menu--Trigger" compact icon size="tiny">
            <Menu.Item
              name="caret square left"
              onClick={() => {
                document.querySelector("#pushable").scrollTo({
                  top: 0,
                  behavior: "smooth"
                });
                this.setState({ visible: !visible });
              }}
            >
              <Icon name="caret square right" />
            </Menu.Item>
          </Menu>
        )}
        <Sidebar.Pushable id="pushable">
          <SideMenu
            visible={visible}
            isSmallScreen={isSmallScreen}
            onToggle={() => this.setState({ visible: !visible })}
            firebase={firebase}
          />

          <Sidebar.Pusher dimmed={visible && isSmallScreen}>
            <Editor />
          </Sidebar.Pusher>
        </Sidebar.Pushable>
      </>
    );
  }
}

export default App;
